/* Table */
table {
  table-layout: fixed;
  margin: 0;
  border-spacing: 0;
  border-bottom: solid 1px #e0e8ef;
  box-shadow: 0 1px 0 0 rgba(204, 204, 204, 0.5);
}

table tr:hover td {
  background-color: #e6f6e5;
}

table tr.active td {
  background-color: #bae4ff !important;
  border-bottom: 1px solid #98d2f6 !important;
}

table td {
  padding: 6px 10px;
  color: #666;
  font-size: 0.825em;
  border-bottom: 1px solid #ebf2f7;
  -ms-word-break: break-all;
  word-wrap: break-word;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
      hyphens: auto;
  vertical-align: top;
}

table td * {
  -ms-word-break: break-all;
  word-wrap: break-word;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
      hyphens: auto;
}

table table {
  width: 100%;
  box-shadow: none;
  border-bottom: none;
}

table table td,
table table th {
  background: none !important;
  font-size: 1em;
}

table th {
  background-color: #ebf2f7;
  padding: 6px 10px;
  color: #a9aaad;
  font-size: 0.825em;
  font-weight: bold;
  text-align: left;
}

table th.thead {
  padding: 7px 10px;
  font-size: 0.688em;
}

table th a {
  color: #a9aaad;
}

table th a.sort-up {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
}

table th a.sort-up i {
  position: relative;
  padding-right: 14px;
}

table th a.sort-up i:before {
  position: absolute;
  top: 6px;
  right: 0;
  content: '';
  height: 0;
  width: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid #a9aaad;
}

table th a.sort-up.sort-down i:before {
  display: none;
}

table th a.sort-up.sort-down i:after {
  position: absolute;
  top: 6px;
  right: 0;
  content: '';
  height: 0;
  width: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 4px solid #a9aaad;
}

table.table-odd {
  border-collapse: separate;
